import { Component, OnDestroy, OnInit } from '@angular/core';
import { ActivatedRoute, Params } from '@angular/router';
import { ComService } from '../core/com.service';
import * as Canlendar from '../../assets/js/calendar';

@Component({
  selector: 'app-server',
  templateUrl: './server.component.html',
  styleUrls: ['./server.component.css'],
})
export class ServerComponent implements OnInit {
  chatList: any = [];
  isServer: boolean = true;
  ws: any;
  rid: any = 0;
  // 
  user: any;
  // 
  userList = [];
  // id
  myID: number;
  isShowRight:boolean = false;
  rightShow: string = '';
  chatElem: any;
  stateMenuShow: boolean = false;
  allHistory: any = [];
  rightHistory: any[] = [];
  // 
  files: any = [];
  // /
  // 
  isShowUserList: boolean = false;
  // 
  historyDate: string = '';
  // isFrame 
  isFrame: boolean = false;
  // 
  isShowMonthCalendar: boolean = false;
   
  constructor(private com: ComService, private route: ActivatedRoute) {}

  ngOnInit(): void {
    this.historyDate = this.com.formatTime('', 'YYYY-MM-DD');
    this.initCalendar();
    window.location.search.indexOf('frame=1') > -1 && (this.isFrame = true);

    this.com.getUserList((data) => {
      if (data && data.length) {
        // 
        let storage = window.localStorage.unread;
        try {
          storage = JSON.parse(storage) || {};
        } catch (e) {
          storage = {};
        }
        data.forEach((d) => {
          // 5s
          storage[d.id] && (d.unreadOption = storage[d.id]);
          d.state = Date.now() - d.note_at / 1000000 < 5000;
        });
        this.userList = data;
        this.changeUser(0);
      }
    });
  }

  userListChange(receiveData) {
    const { data,chatUnread,state } = receiveData;
    
    const fid = data.fid;
    let userIndex = this.userList.findIndex((u) => u.id === fid );
    if(state === 0){
      let isInUserList = userIndex > -1;
      if (!isInUserList) {
        this.com.getUserInfo(fid, (userInfo) => {
                this.com.notifyAndUnreadPlus(  data, chatUnread,userInfo);
                this.userList.unshift(userInfo);
                // this.userList = [userInfo ,...this.userList]
                if(!this.user){
                  this.changeUser(0);
                }
                
        });
      }else{
        this.com.notifyAndUnreadPlus(data,chatUnread,this.userList[userIndex])
      }    
    }else if (state === 3){
      userIndex > -1 && (this.userList[userIndex].state = data.data === 'online');
    }
  }
  clickImg(e) {
    if (e.target.tagName.toLowerCase() === 'img') {
      this.com.previewOption.isShowMask = true;
      this.com.previewOption.src = e.target.src;
    }
  }
  initCalendar() {
    let callback = (y, m, d) => {
      this.historyDate = this.com.formatTime(
        y + '-' + m + '-' + d,
        'YYYY-MM-DD'
      );
    };
    new Canlendar({
      el: '#schedule-box',
      clickCb: callback,
      nextMonthCb: callback,
      nextYeayCb: callback,
      prevMonthCb: callback,
      prevYearCb: callback,
      lang: this.com.localId,
    });
  }
  keepEnd: boolean = false;
  fileEmiter(e) {
    this.keepEnd = !this.keepEnd;
  }
  // 
  getSpecialHistory(date) {
    if (this.allHistory.length) {
      this.rightHistory = this.allHistory.filter((el) => {
        return el.timeFormat.indexOf(date) > -1;
      });
      this.isShowMonthCalendar = false;
    }
  }
  showUserList(e) {
    e.stopPropagation();
    this.isShowUserList = !this.isShowUserList;
  }
  // 
  changeUser(i) {
    this.user = this.userList[i];
    this.getHistory();
  }
  historyData:any = {data:[]};
  containerClick(e){    
    let id = e.target.id;     
    if(id === 'historyIcon'){
      this.isShowRight = true;
      this.getHistory();       
    }
    this.isShowUserList = false;
  }
  showRightHistory(data){
    // 
    this.rightShow = 'right-show';
    this.rightHistory = data;
    this.allHistory = data;
  }
  getHistory() {
    this.com.getHistory( this.user.id)
    .subscribe((res: any) => {
     if (!this.myID) {
       this.myID = Number(res.headers.get('Uid'));
     }
     let ret = res.body;
     this.com.doHttpResponse(ret, (data) => {
       // this.com.doHistory(data );
       this.historyData = {uid:this.myID,data};       
       this.isShowRight && this.showRightHistory(data)
     },null,true);
   });
  }
   

  catchTab(e, callback) {
    callback && callback();
    e.stopPropagation();
  }

  hideShowing() {
    this.stateMenuShow = false;
    this.isShowUserList = false;
    this.isShowMonthCalendar = false;
  }
  imgSrc: string = '';

  rightHitoryShow(toggle) {
    if (!toggle) {
     this.getHistory();
      
    } else {
      this.closeHistory();
    }
  }
  closeHistory() {
    this.rightHistory = [];
    this.rightShow = '';
  }
  showMonthCalendar() {
    this.isShowMonthCalendar = !this.isShowMonthCalendar;
  }
  chooseDateConfirm() {
    this.getSpecialHistory(this.historyDate);
  }
}
