import {
  Component,
  EventEmitter,
  Input,
  OnInit,
  Output,
  SimpleChanges,
} from '@angular/core';
import { ComService } from 'src/app/core/com.service';

@Component({
  selector: 'chat-dailog',
  templateUrl: './chat-dailog.component.html',
  styleUrls: ['./chat-dailog.component.css'],
})
export class ChatDailogComponent implements OnInit {
  @Input() isServer: boolean;
  @Input() noUser: boolean;
  @Input() cuUser: any;
  @Input() historyData: boolean;

  @Output() unreadChange = new EventEmitter();
  @Output() userListChange = new EventEmitter();

  chatList: any = [];
  myID: any;
  history: any = [];
  chatUnread: any = {};
  chatElem: any;
  isWindowActive: boolean = false;

  constructor(public com: ComService) {}

  ngOnInit(): void {
    let search = window.location.search.substring(1);
    let random = Date.now().toString();
    this.chatElem = document.getElementById('chat');
    this.pollingStart(random, search);
    this.windowActive();
  }

  ngOnChanges(changes: SimpleChanges): void {
    
    const history = changes.historyData;
    if (history && history.currentValue !== history.previousValue) {
      this.chatUnread = { id: [], unread: 0 };
      
      this.doHistory(history.currentValue.data, history.currentValue.uid);
    }
  }

  windowActive() {
    window.onblur = () => {
      this.isWindowActive = false;
      
    };
    window.onfocus = () => {
      // - -
      this.isWindowActive = true;
      this.changeUnread();
      // this.setISendReaded();      
    };
  }
  // 
  changeUnread() {
    for (let i = 0; i < this.com.IReceived.length; i++) {
      let el = this.com.IReceived[i];
      let msg: any = { id: el.id, type: 1 };
      this.isServer && (msg.rid = this.cuUser.id);      
      if (this.isInView('chatList' + el.id)) {
        this.com.IReceived.splice(i,1)
        this.com.sendMsg(msg,null,null,this.isServer);
        i--;        
      } else {        
        break;
      }
    }
    this.chatUnread.unread = this.com.IReceived.length;
  }
  retryTimes = 0;
  pollingStart(random, search) {
     
    var t = null;
    this.com.subscriber = this.com
      .pollingStart(random, search, this.isServer)
      .subscribe((res: any) => {    
        try{   
        if (!this.myID) {
          this.myID = Number(res.headers.get('Uid'));
        }
        // 
        let isNewConnect = res.headers.get('Request-Times') === '1';
        let ret = res.body;

        this.com.doHttpResponse(ret, (data) => {
          if (isNewConnect) {
            if (!this.isServer) {
              // 
              data && data.length && this.doHistory(data);
            }
          } else {
            // 
            data && data.length && this.doPolling(data);
          }
        },null,this.isServer );

        // -      
        this.pollingStart(random, search);
      }catch(e){
        clearTimeout(t);
        if(res?.msg){
          this.com.showAlert(res.msg)
        }
        t= setTimeout(()=>{
        this.pollingStart(random, search);},2000);
      }
          // this.com.isLogin && this.pollingStart(random, search);
          // this.retryTimes ++;
      });
  }

  doHistory(data, uid?) {
    uid && (this.myID = uid);
    this.com.IReceived = [];
    this.com.ISend = [];
    this.attachSPTWidthArr(data);
    this.chatList = data;
    this.jumpToChatBottom();
    this.history = data;
    this.setReadInfo(data);
    this.clearUnreadMsg();
  }
  tmpTime = '';
  attachSPT(d) {
    const isServer = this.isServer;
    let isIsend = d.fid === this.myID;
    let isSysMsg = d.fid === 0;

    d.isServer = isSysMsg ? true : isServer ? isIsend : !isIsend;
    d.position = isServer ? (isSysMsg ? false : !isIsend) : !isIsend;
    let fromNowTime = this.com.timeFromNow(d.time / 1000000);
    d.timeStr = this.tmpTime === fromNowTime ? '' : fromNowTime;
    this.tmpTime !== fromNowTime && (this.tmpTime = fromNowTime);
    d.timeFormat = this.com.formatTime(d.time / 1000000, 'YYYY-MM-DD hh:mm:ss');
  }
  attachSPTWidthArr(data) {
    data.forEach((d) => {
      this.attachSPT(d);
    });
  }
  // --
  scrollTop = 0;
  scrollToBottom(e) {
    setTimeout(() => {
      const chatWin = e.target;
      if (this.scrollTop < chatWin.scrollTop) {
        // 
        this.changeUnread();
        if (
          chatWin.scrollTop + chatWin.offsetHeight + 10 >=
          chatWin.scrollHeight
        ) {
          // -unread
          this.clearUnreadMsg();
        }
      }
      this.scrollTop = chatWin.scrollTop
    }, 1000);
  }

  // 
  setReadInfo(data) {
    let len = data.length;
    this.chatUnread = { unread: 0, id: [] };
    let rightMsgReaded = false;
    let rightStopIndex = -1;
    let leftMsgReaded = false;
    let leftStopIndex = -1;
    for (let i = len; i--; ) {
      let d = data[i];
      let isDbReaders = d.readers.split(',').length > 1;
      let isIsend = d.fid === this.myID;
      let isSysMsg = d.fid === 0;
      // == 
      this.isServer && (isIsend = isSysMsg || isIsend);
      if (isIsend) {
        leftMsgReaded = true;
        leftStopIndex = i;
        if (isDbReaders) {
          // 
          rightMsgReaded = true;
          // 
          rightStopIndex = i;
          break;
        } else {
          // 
          d.readed = rightMsgReaded;
          // 
        }
      } else {
        // -
        rightMsgReaded = true;
        rightStopIndex = i;

        if (isDbReaders) {
          // 
          leftMsgReaded = true;
          leftStopIndex = i;
          break;
        } else {
          // 
          if (!leftMsgReaded) {
            this.chatUnread.unread++;
            this.chatUnread.id.push(d.id);
          }

          !this.chatUnread.chatID &&
            (this.chatUnread.chatID = 'chatList' + d.id);
        }
      }
      if (rightMsgReaded && leftMsgReaded) break;
    }

    // 
    if (rightMsgReaded && rightStopIndex > -1) {
      for (let i = rightStopIndex + 1; i--; ) {
        data[i].readed = true;
      }
      for (let i = rightStopIndex + 1; i < data.length; i++) {
        data[i].fid === this.myID &&
          (this.com.ISend[this.com.ISend.length] = i);
      }
    }

    if (leftMsgReaded && leftStopIndex > -1) {
      for (let i = leftStopIndex + 1; i < data.length; i++) {
        data[i].fid !== this.myID &&
          (this.com.IReceived[this.com.IReceived.length] = data[i]);
      }
    }
    
  }

  // 
  isInView(id) {
    let nowAt = document.getElementById(id);
    if (nowAt) {
      let f = nowAt.offsetTop;
      let h = nowAt.offsetHeight;
      let h1 = this.chatElem.clientHeight;
      let t = this.chatElem.scrollTop;
      return f > t && f + h < t + h1;
    }
    return false;
  }
  // 
  clearUnreadMsg() {
    // 0
    this.chatUnread = { unread: 0, id: [], chatID: '' };
    if (this.isServer) {
      this.cuUser &&
        this.cuUser.id &&
        (this.cuUser.unreadOption = { unread: 0, id: [], chatID: '' }) &&
        this.com.updateUnreadStorage(this.cuUser.id, {
          unread: 0,
          id: [],
          chatID: '',
        });
    }
  }

  // 
  readIt() {
    this.jumpToChatBottom();
    this.chatUnread = { unread: 0, id: [] };
    this.isServer &&
      this.com.updateUnreadStorage(this.cuUser.id, this.chatUnread);
    this.setIReceivedReaded();
    this.setISendReaded();
  }

  doPolling(data) {
    data.forEach((d) => {
      let fid = d.fid;
      let isISend = fid === this.myID;
      let type = d.type;
      switch (type) {
        // 
        case 0:
          this.doType0(isISend, d);
          break;
        // 
        case 1:
          this.doType1(d, isISend);
          break;
        case 2:
          this.doType2(d, isISend);
          break;
        // 
        case 3:
          this.doType3(isISend, d);
          break;
      }
    });
  }
  // 
  doType1(d, isISend) {
    // 
    if (!isISend) {
      if (this.isServer) {
        // 
        // --
        if (!isISend) {
          // 
          let isCuUserSend = this.cuUser.id === d.fid;
          if (isCuUserSend) {
            // 
            let i = this.chatList.findIndex((x) => x.id === d.id);
            // i > -1 && (this.chatList[i].readed = true)
            if (i > -1) {
              for (let j = i; j >= 0; j--) {
                this.chatList[j].readed = true;
              }
            }
          }
        }
      } else {
        // 
        let i = this.chatList.findIndex((x) => x.id === d.id);
        // i > -1 && (this.chatList[i].readed = true);
        if (i > -1) {
          for (let j = i; j >= 0; j--) {
            this.chatList[j].readed = true;
          }
        }
      }
    }
  }
  //  
  doType2(d, isISend) {
    // 
    if (!isISend) {
      if (d.data === 'offline') {
        let who = this.com.setName(
          d,
          this.isServer ? this.cuUser : null,
          this.isServer
        );
        this.com.showAlert(
          who + { en: 'offline', zh: '' }[this.com.localId]
        );
        if (this.isServer) {
          let i = this.chatList.findIndex((x) => x.id === d.fid);
          i > -1 && (this.chatList[i].state = false);
        }
      }
    }
  }
  // 
  doType3(isISend, d) {
    isISend &&
      this.com.showAlert(
        {
          en: 'User ' + d.fid + 'is ' + d.data,
          zh: '' + d.fid + (d.data === 'online' ? '' : '') + '',
        }[this.com.localId]
      );
    //  
    if (this.isServer) {
      this.userListChange.emit({ state: 3, data: d });
    }
  }

  doType0(isISend, d) {
    let fid = d.fid;
    const isServer = this.isServer;
    // this.cuUsertrue
    let isCuUserSend = isServer ? this.cuUser && this.cuUser.id === fid : !isISend;
    // 
    this.attachSPT(d);
    if (isISend) {
      this.msgAssgin(d);
      if (this.isWindowActive) {
        this.setIReceivedReaded();
      }
    } else {
      if (isCuUserSend) {
        this.com.IReceived.push(d);
        
        this.chatList.push(d);
        if (this.isWindowActive) {
          this.jumpToChatBottom();
          this.setIReceivedReaded();
          this.setISendReaded();
        } else {
          
          this.com.notifyAndUnreadPlus(d, this.chatUnread, this.cuUser);
        }
      } else {
        let unread = window.localStorage.unread;
        unread = unread ? JSON.parse(unread) : {};
        this.chatUnread = { id: [], unread: 0 };
        
        this.userListChange.emit({
          state: 0,
          chatUnread:unread[d.fid] || { id: [], unread: 0 },
          data: d,
        });
      }
    }
  }

  setIReceivedReaded() {
    this.com.IReceived.forEach((el) => {
      let msg: any = { id: el.id, type: 1 };
      this.isServer && (msg.rid = this.cuUser.id);
      this.com.sendMsg(msg,null,null,this.isServer);
    });
  }

  // 
  msgAssgin(d) {
    let i = this.chatList.findIndex((x) => x.mark === d.mark);
    i === -1 ? this.chatList.push(d) : Object.assign(this.chatList[i], d);
    this.com.ISend.push(i === -1 ? this.chatList.length : i);
  }
  // 
  setISendReaded() {
    this.com.ISend.forEach((el) => {
      this.chatList[el].readed = true;
    });
    this.com.ISend = [];
  }

  sendListen(content) {
    content.isServer = this.isServer;
    content.position = false;
    this.chatList.push(content);
  }

  chatListChange(msg) {
    const { send, content } = msg;
    if (send === 0) {
      //push
      this.chatList.push(content);
      this.isServer && (content.rid = this.cuUser.id);
    } else {
      // 
      let i = this.chatList.findIndex((x) => x.mark === content.mark);
      i > -1 && (this.chatList[i].isUploaded = send);
    }
    this.jumpToChatBottom();
  }

  jumpToChatBottom() {
    setTimeout(() => {
      this.chatElem.scrollTop = this.chatElem.scrollHeight;
    }, 1000);
  }
}
