import {
  HttpClient,
  HttpErrorResponse,
  HttpEventType,
  HttpRequest,
  HttpResponse,
} from '@angular/common/http';
import { Inject, Injectable, LOCALE_ID } from '@angular/core';
import { Title } from '@angular/platform-browser';
import { NavigationEnd, Router } from '@angular/router';
import { of, throwError, Subject } from 'rxjs';
import { catchError, filter, map, retry, tap } from 'rxjs/operators';
@Injectable({
  providedIn: 'root',
})
export class ComService {
  
  constructor(
    private http: HttpClient,
    private router: Router,
    private titleService: Title,
    @Inject(LOCALE_ID) public localId: string
  ) {}

  // 
  // alert: { msg: string; show: boolean } = { msg: '', show: false };
  alert = new Subject();
  ws: any;
  data: any = {};
  previewOption: { isShowMask: boolean; src?: string } = {
    isShowMask: false,
  };
  subscriber = null; // polling
  sub = new Subject<any>();
  api = {
    login: '/api/auth/login', //post
    register: '/api/auth/register', //post
    message: '/api/message/', //post
    polling: '/api/message/polling', //get
    history: '/api/message/record', //get
    userList: '/api/user/list', // get
    logout: '/api/auth/logout', //post
    userInfo: '/api/user/', //{uid},get
    files: '/file/', // 
  };
  who: any = null; // 
  isLogin: boolean = true;
  // 
  reconnect: boolean = false;
  ISend: any = [];
  IReceived: any = [];

  setTitle() {
    this.router.events
      .pipe(
        filter(event => event instanceof NavigationEnd),
        map(() => this.router)
      )
      .subscribe((event) => {
        const titles = this.getTitle(this.router.routerState, this.router.routerState.root);
        const title = titles[titles.length - 1];
        // console.log(title);f
        if (title) {
          this.titleService.setTitle('Chat System for ' + title);
        }
      });
  }

  public getTitle(state, parent) {
    const data = [];
    if (parent && parent.snapshot.data && parent.snapshot.data.title) {
      data.push(parent.snapshot.data.title);
    }
    if (state && parent) {
      const tmp = this.getTitle(state, state.firstChild(parent));
      tmp.forEach((el)=>{
        data.push(el);
      })
      
    }
    return data;
  }

  // 
  formatTime(
    date: string | number | Date = '' /*  */,
    fmt = 'YYYY-MM-DD' /*YYYY-MM-DD hh:mm:ss */
  ) {
    //ios
    if (typeof date === 'string') {
      date = date.replace(/-/g, '/');
    }
    date = date ? new Date(date) : new Date();
    var o = {
      'Y+': date.getFullYear(), //
      'M+': date.getMonth() + 1, //
      'D+': date.getDate(), //
      'h+': date.getHours(), //
      'm+': date.getMinutes(), //
      's+': date.getSeconds(), //
      'q+': Math.floor((date.getMonth() + 3) / 3), //
      S: date.getMilliseconds(), //
    };
    if (/(Y+)/.test(fmt)) {
      fmt = fmt.replace(RegExp.$1, (o['Y+'] + '').substr(4 - RegExp.$1.length));
    }
    for (var k in o) {
      if (new RegExp('(' + k + ')').test(fmt)) {
        fmt = fmt.replace(
          RegExp.$1,
          RegExp.$1.length == 1
            ? o[k]
            : ('00' + o[k]).substr(('' + o[k]).length)
        );
      }
    }
    return fmt;
  }
  timeFromNow(time) {
    //
    const timestamp = Date.now();
    const createTime = new Date(time);
    time = createTime.getTime();
    const diff = timestamp - time;
    if (diff < 60 * 1000) {
      return '';
    } else if (diff < 60 * 60 * 1000) {
      return (
        Math.floor(diff / (60 * 1000)) +
        { en: ' minutes ago', zh: '' }[this.localId]
      );
    } else if (diff < 24 * 60 * 60 * 1000) {
      return (
        Math.floor(diff / (60 * 60 * 1000)) +
        { en: ' hours ago', zh: '' }[this.localId]
      );
    } else {
      return this.formatTime(time, 'YYYY-MM-DD hh:mm');
    }
  }

  showAlert(msg,duration?:number) {
    console.log('showAlert' );
    this.alert.next({show:true,msg:msg});
    
    
    setTimeout(() => {
     this.alert.next({show:false,msg})
    }, duration??2000);
  }

  clearCookie() {
    var date = new Date();
    date.setTime(date.getTime() - 10000);
    var keys = document.cookie.match(/[^ =;]+(?=\=)/g);
    if (keys) {
      for (var i = keys.length; i--; )
        document.cookie =
          keys[i] + '=0; expire=' + date.toDateString() + '; path=/';
    }
  }

  doHttpResponse(res, success, fail?, isServer?) {
    
    let code = res.code;
    if (code) {
      if (code === 3 || code === 6) {
        // 3 6
        this.subscriber.unsubscribe();
        // -cookie-
        
        if (isServer) {
          this.showAlert(
            this.localId === 'en'
              ? 'Login invalid, please login again'
              : ''
          );
          // --
          setTimeout(() => {
            this.router.navigate(['/auth/login']);
          }, 2000);
        } else {
          
          this.clearCookie();
        }
      } else {
        fail ? fail(res) : this.showAlert(res.msg);
      }
    } else {
      // 
      success && success(res.data);
    }
  }

  // 
  sendMsg(msg, success?, fail?,isServer?) {
    console.log('sendMsg isServer==: ', msg,isServer);
    this.http
      .post(this.api.message, msg)
      .pipe(
        catchError(
          this.handleError('sendMsg', () => {
            fail && fail();
          })
        )
      )
      .subscribe((res) => {
        this.doHttpResponse(
          res,
          (data) => {
            success && success(data);
          },
          fail,
          isServer
        );
      });
  }
  private handleError(uri: string, failCallback?) {
    return (error: HttpErrorResponse) => {
      console.log('uri error:== ',error);
      
      this.reconnect = true;
      if (failCallback) {
        console.log('fail()');
        failCallback();
      } else {
        this.showAlert('http request ' + uri + ' error ');
         
      }
       
      return of({code:-1,msg:'',data:null})
    
      return throwError(
        {
          en: 'Something bad happened; please try again later.',
          zh: '',
        }[this.localId]
      );/**/
    };
  }
  // 
  pollingStart(random: string, domain?: string, isServer?: boolean) {
    let options: any = {
      observe: 'response',
    };
    return this.http
      .get(
        this.api.polling +
          '?random=' +
          random +
          (domain ? '&' + domain : '') +
          (isServer ? '&role=1' : ''),
        options
      )

      .pipe(
        tap(() => (this.reconnect = false)),
        
        catchError(
          this.handleError('polling', () => {
            this.pollingStart(random, domain, isServer);
          })
        )
      );
  }
  // 
  preview(file, callback) {
    var reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = (e) => {
      callback(e.target.result);
    };
  }
  // 
  videoPreview(file, callback) {
    var url = null;
    try {
      if (window.URL != undefined) {
        // mozilla(firefox)
        url = window.URL.createObjectURL(file);
      } else if (window.webkitURL != undefined) {
        // webkit or chrome
        url = window.webkitURL.createObjectURL(file);
      }
      callback(url);
    } catch (e) {
      // 
    }
  }
  // 
  uploadFile(fileObj, callback, progressCallback?) {
    const formData = new FormData();
    formData.append('file', fileObj.file);
    let api = 'https://io.imdraw.com:444';
    const req = new HttpRequest('POST', api, formData, {
      reportProgress: true,
    });
    this.http
      .request(req)
      .pipe(
        catchError(
          this.handleError('uploadFile', () => {
            callback(fileObj, true);
            this.showAlert(
              { en: 'File Server Error', zh: '!' }[this.localId]
            );
          })
        )
      )
      .subscribe((event: any) => {
        if (event.type === HttpEventType.UploadProgress) {
          fileObj.uploadProgress = Math.round(
            (100 * event.loaded) / event.total
          );
          progressCallback && progressCallback(fileObj);
        } else if (event instanceof HttpResponse) {
          let body: any = event.body;
          fileObj.src = api + '/' + body.key;
          fileObj.type === 'file' && (fileObj.src += '?operation=download');
          callback(fileObj);
        }
      });
  }

  notifyAndUnreadPlus(d, chatUnread, cuUser?) {
    cuUser && (cuUser.state = true);
    let name = this.setName(d, cuUser, !!cuUser);
    console.log('',cuUser);
    this.deskNotify(name, d.data);
    this.setUnreadPlus(chatUnread, d, cuUser);
  }

  setName(d?, user?, isServer?) {
    return isServer
      ? user.name
        ? user.name
        : { en: 'User', zh: '' }[this.localId] + d.fid
      : { en: 'Server Reply', zh: '' }[this.localId];
  }

  setUnreadPlus(chatUnread, msg, cuUser) {
    chatUnread = chatUnread || {
      unread: 0,
      chatID: '',
      id: [],
    };

    chatUnread.unread++;
    !chatUnread.chatID && (chatUnread.chatID = 'chatList' + msg.id);
    chatUnread.id?.push(msg.id);

    cuUser &&
      (cuUser.unreadOption = chatUnread) &&
      this.updateUnreadStorage(cuUser.id, chatUnread);
  }

  updateUnreadStorage(id, unreadOption) {
    let storage: any = window.localStorage.unread;
    try {
      storage = (storage && JSON.parse(storage)) || {};
    } catch (e) {
      storage = {};
    }
    storage[id] = unreadOption;
    window.localStorage.unread = JSON.stringify(storage);
  }

  // 
  getUserList(success) {
    this.http
      .get(this.api.userList)
      .pipe(catchError(this.handleError('getUserList')))
      .subscribe((res) => {
        this.doHttpResponse(res, success,null,true);
      });
  }
  getUserInfo(id, cb) {
    this.http
      .get(this.api.userInfo + id)
      .pipe(catchError(this.handleError('getUserInfo')))
      .subscribe((res: any) => {
        this.doHttpResponse(res, cb,null,true);
      });
  }
  login(data) {
    this.http
      .post(this.api.login, data)
      .pipe(catchError(this.handleError('login')))
      .subscribe((res) => {
        this.doHttpResponse(res, () => {
          this.showAlert(
            { en: 'login Successful!', zh: '' }[this.localId]
          );
          this.isLogin = true;
          // 
          setTimeout(() => {
            this.router.navigate(['/server']);
          }, 2000);
        },null,true);
      });
  }
  register(data) {
    this.http
      .post(this.api.register, data)
      .pipe(catchError(this.handleError('register')))
      .subscribe((res) => {
        this.doHttpResponse(res, () => {
          this.showAlert(
            {
              en: 'Register successfully, jump to login immediately',
              zh: '',
            }[this.localId]
          );
          setTimeout(() => {
            this.router.navigate(['/auth/login']);
          }, 2000);
        });
      });
  }

  logout() {
    this.http
      .post(this.api.logout, null)
      .pipe(catchError(this.handleError('logout')))
      .subscribe((res) => {
        this.doHttpResponse(res, () => {
          this.showAlert(
            { en: 'Log out success !', zh: '!' }[this.localId]
          );
          setTimeout(() => {
            this.router.navigate(['/auth/login']);
          }, 2000);
        },null,true);
      });
  }
  getHistory(id) {
    let options: any = {
      observe: 'response',
    };
    return this.http
      .get(this.api.history + (id ? '?id=' + id : ''), options)
      .pipe(catchError(this.handleError('getHistory')));
  }

  notifyInstanceEvent(content, title) {
    var instance = new Notification(title, {
      body: content,
      icon: '/assets/images/desk-logo.png',
      tag: '1212212',
      // sound:'/assets/sounds/msg.wav',
      renotify: true,
    });
    instance.onclick = function (e) {
      window.focus();
      instance.close();
      //  
    };
    instance.onerror = function () {};
    
    instance.onshow = function () {
      try {
        var msg = new Audio();
        msg.src = '/assets/sounds/msg.wav';
        msg.play();
        console.log('');
      } catch (e) {
        console.log('');
      }
    };
    instance.onclose = function () {};
  }
  // 
  timer: any;
  deskNotify(title, msg) {
    var Notification = window.Notification;
    let div = document.createElement('div');
    div.innerHTML = msg;
    let content = '';
    Array.from(div.childNodes).forEach((el: any) => {
      let nodeType = el.nodeType;
      if (nodeType === 1) {
        // 
        let id = el.id;
        if (id.substring(0, 3) === 'img') {
          content += { en: '[Image]', zh: '[]' }[this.localId];
        } else if (id.substring(0, 4) === 'file') {
          content +=
            { en: '[File]', zh: '[]' }[this.localId] + el.innerText;
        } else if (id.substring(0, 5) === 'video') {
          content += { en: '[Video]', zh: '[]' }[this.localId];
        } else {
          content += el.innerText;
        }
      } else if (nodeType === 3) {
        // 
        content += el.textContent;
      }
    });
    content = content.substring(0, 100) + (content.length > 100 ? '...' : '');
    console.log(': ', content);

    
    if (Notification) {
      //
      if (Notification.permission == 'granted') {
        //
        console.log('2');
        this.notifyInstanceEvent(content, title);
      } else {
        //(Notification.requestPermission())
        Notification.requestPermission((status) => {
          if (status === 'granted') {
            console.log('1');
            this.notifyInstanceEvent(content, title);
          } else {
            //
            console.log('');
            this.showAlert('');
            return false;
          }
        });
      }
    } else {
      console.log('');
      //(IE)
      var index = 0;
      clearInterval(this.timer);
      this.timer = setInterval(function () {
        let titleElem = document.getElementsByName('title')[0];
        if (index % 2) {
          titleElem.innerText = '' + title; //
        } else {
          titleElem.innerText =
            { en: '[new message]', zh: '' }[this.localId] + title;
        }
        index++;

        if (index > 20) {
          clearInterval(this.timer);
        }
      }, 500);
    }
  }
}
