import { Component, EventEmitter, Input, OnInit, Output, SimpleChanges } from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';
import { ComService } from 'src/app/core/com.service';

@Component({
  selector: 'send-area',
  templateUrl: './send-area.component.html',
  styleUrls: ['./send-area.component.css'],
})
export class SendAreaComponent implements OnInit {
  @Input() isServer: boolean;
  @Input() noUser: boolean;

  @Output() chatListChange = new EventEmitter()
 
  target = null;
  showPlaceHolder = true;
  editText: string = '';
  files: any = [];

  constructor(public com: ComService, private _sanitizer: DomSanitizer) {}

  ngOnInit(): void {
    this.target = document.getElementById('editDiv');
    this.setEditDivUsable();
  }
  ngOnChanges(changes: SimpleChanges): void {    
    this.target && changes.noUser.currentValue !== changes.noUser.previousValue &&
      this.setEditDivUsable();
  }

  getFileObj(file) {
    /* ===region== 
    .doc application/msword
    .docx application/vnd.openxmlformats-officedocument.wordprocessingml.document
    .xls application/vnd.ms-excel
    .xlsx  application/vnd.openxmlformats-officedocument.spreadsheetml.sheet
    .ppt application/vnd.ms-powerpoint
    .pptx application/vnd.openxmlformats-officedocument.presentationml.presentation
    .pdf application/pdf
    .zip application/zip, application/octet-stream, application/x-zip-compressed, multipart/x-zip
    .rar application/x-rar-compressed, application/octet-stream
    .gz application/x-gzip
    .txt 'text/plain'
    .avi 	video/x-msvideo
    .wav audio/x-wav
    .mp4 video/mp4*/
  
    const permitFileType = [
      'application/zip', //0-zip
      'application/octet-stream',//1-zip
      'application/x-zip-compressed',//2-zip
      'multipart/x-zip',//3-zip
      'application/x-rar-compressed',//4-zip
      'application/x-gzip',//5-zip
      'application/msword',//6-doc
      'application/vnd.openxmlformats-officedocument.wordprocessingml.document',//7-doc
      'application/vnd.ms-excel',//8-xls
      'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet',//9-xls
      'application/vnd.ms-powerpoint',//10-ppt
      'application/vnd.openxmlformats-officedocument.presentationml.presentation',//11-ppt
      'application/pdf',//12-pdf
      'text/plain',//13-txt
    ];

    const videoType = ['video/x-msvideo', 'video/mp4','video/quicktime'];
    const type = file.type;
    
    
    let obj = null;
    let i =  permitFileType.indexOf(type);
    if(i>-1){
      obj = { type: 'file', fid: 'file' + Date.now(),name:file.name, file ,fileType:i<5?'zip':i<8?'doc':i<10?'xls':i<12?'ppt':i===12?'pdf':'txt'}
    } else if(videoType.indexOf(type) > -1){
      obj = { type: 'video', fid: 'video' + Date.now(), file }
    }
    return obj;
  }

  getFile(e) {
    let file = e.target.files[0];
    // 
    const size = file.size;
    let obj = null;
    if(size > 10485760){
      // 10M
      this.com.showAlert({en:'Picture / video / file size cannot exceed 10MB',zh:'//10MB'}[this.com.localId]);
      return;
    }
    // 
    if (file.type.indexOf('image') > -1) {
      // 
      this.com.preview(file, (src) => {
         obj = { type: 'img', fid: 'img' + Date.now(), src, file };
        this.files.push(obj);
        this.newFileNode(obj)
      });
    } else if (obj = this.getFileObj(file)) {
      this.files.push(obj);  
      this.newFileNode(obj)    
    } else {
      this.com.showAlert(
        {
          en: 'This picture / video / file format is not supported',
          zh: '//',
        }[this.com.localId]
      );
    }
    e.target.value = '';
  }



  newFileNode(file) {
    
    if (file) {
      const fileType = file.type;
      let span: any = document.createElement('span');
      span.id = file.fid;
      span.contentEditable = false;
      span.className = 'd-inline-block';
      fileType === 'file' && (span.className += ' file-box');
      if (fileType === 'img') {
        let img: any = document.createElement('img');
        img.src = file.src;
        img.onerror = "this.src='/assets/images/error.jpg'";
        span.appendChild(img);
      } else if(fileType==='file'){
        let icon = document.createElement('i');
        icon.className = 'iconfont icon-' + file.fileType;
        span.appendChild(icon);
        let link = document.createElement('a');
        link.href = file.src;
        link.textContent = file.name;
        link.className = 'file-name';
        span.appendChild(link);
      }else if(fileType ==='video'){
        let video:any = document.createElement('video');
        video.setAttribute('controls','controls');
        video.style="width:250px;height:250px;"
        this.com.videoPreview(file.file,(url)=>{
          video.src = url;
        })
        span.appendChild(video)
      }
      this.target.appendChild(span);
      this.keepLastIndex(this.target);
    }
  }
  setBorder(d, e) {
    d.border = !d.border;
    let range = document.createRange();
    range.selectNodeContents(e.target); // document.getElementById('test')
    range.collapse(false);
    let sel = window.getSelection();
    sel.removeAllRanges();
    sel.addRange(range);
    this.editFocus();
  }
  // 
  editPaste(e) {
    var cbd = e.clipboardData;    
    var ua = window.navigator.userAgent;

    //  Safari  return
    if (!(e.clipboardData && e.clipboardData.items)) {
      return;
    }
    // MacChrome49 FinderBug Hack
    if (
      cbd.items &&
      cbd.items.length === 2 &&
      cbd.items[0].kind === 'string' &&
      cbd.items[1].kind === 'file' &&
      cbd.types &&
      cbd.types.length === 2 &&
      cbd.types[0] === 'text/plain' &&
      cbd.types[1] === 'Files' &&
      ua.match(/Macintosh/i) &&
      Number(ua.match(/Chrome\/(\d{2})/i)[1]) < 49
    ) {
      return;
    }
    !cbd.items.length &&
      this.com.showAlert(
        { en: 'Only screenshots can be pasted', zh: '' }[
          this.com.localId
        ]
      );
    for (var i = 0; i < cbd.items.length; i++) {
      var item = cbd.items[i];
      if (item.kind == 'file') {
        // blob  
        var blob = item.getAsFile();
        if (blob.size === 0) {
          return;
        }
      }
      //  -,blob
      if (item.type.indexOf('image') > -1) {
        e.preventDefault();
        this.com.preview(blob, (src) => {
          // imgdata
          let file = {
            type: 'img',
            src,
            file: blob,
            fid: 'img' + Date.now(),
          };
          this.files.push(file);
          this.newFileNode(file);
          this.keepLastIndex(this.target);
        });
      }
    }
  }
  // 
  keepLastIndex(obj) {
    if (window.getSelection) {
      //ie11 10 9 ff safari
      obj.focus(); //ff
      var range = window.getSelection(); //range
      range.selectAllChildren(obj);
      range.collapseToEnd();
    }
  }
  setEditDivUsable() {
    
    if (!this.isServer || (this.isServer && !this.noUser) )
    this.target.setAttribute(
      'contenteditable',
      'true'
    );
  }
  editFocus() {
    this.showPlaceHolder = false;
  }
  editBlur(e) {
    this.editText = e.target.innerHTML;
    this.showPlaceHolder = !this.editText;
  }

  editInput(e) {
    //
  }
  editKeyUp(e) {
    if (e.keyCode === 13) {
      // 
      this.send();
    }
  }

  clickImg(e) {
    this.com.previewOption.isShowMask = true;
    this.com.previewOption.src = e.target.src;
  }

  send() {
    let html = this.target.innerHTML.trim().replace(/<br>/g,''); 
   
    let div = document.createElement('div');
    div.innerHTML = html;
    
    if (html) {
      // editText
      let content: any = { data: html, mark: Date.now().toString() };
      // 
      content.isServer = this.isServer;
      content.position = false;
      this.chatListChange.emit({send:0,content})
     

      let count = 0;
      this.files.forEach((file, i) => {
        html.indexOf(file.fid) === -1 && this.files.splice(i, 1);
      });

      if (this.files.length) {
        // files 
        this.files.forEach((el) => {
          this.com.uploadFile(
            el,
            (file, err?) => {
              if (err) {
                // 
                this.chatListChange.emit({send:-1,content})               
              } else {
                count++;
                let span = null;
                
                Array.from(div.childNodes).forEach((el: any) => {
                  el.id === file.fid && (span = el);
                });
                try {
                  if (file.type === 'img' || file.type==='video') {
                    span.children[0].setAttribute('src', file.src);
                  } else if (file.type === 'file') {
                    span.children[1].setAttribute('href', file.src);
                    span.children[1].setAttribute('download', file.name);
                  } 
                } catch (e) {}
                if (count === this.files.length) {
                  content.data = div.innerHTML;
                  
                  this.sendMsg(content);
                }
              }
            },
            (file) => {
              // chatList               
              content.uploadProgress = file.uploadProgress;
            }
          );
        });
      } else {
        // 
        this.sendMsg(content);
      }
    } else {
      this.com.showAlert(
        { en: 'Please fill in the message', zh: '' }[this.com.localId]
      );
    }
    this.target.innerHTML = '';
  }
 
  sendMsg(content) {
    this.com.sendMsg(
      content,
      (data) => {
        this.files = [];
        this.chatListChange.emit({send:1,content})
      },
      (res) => {
        // -
        
        if (res && res.msg === '') {
          let offline: any = {
            data: {
              en:
                'Customer service is not online, please visit again in a few minutes',
              zh: ',',
            }[this.com.localId],
            mark: Date.now().toString(),
          };
          offline.isServer = true;
          offline.position = true;
          this.chatListChange.emit({send:0,content:offline})          
        } else {
          this.chatListChange.emit({send:-1,content})          
          this.com.showAlert({en:'Send failed !',zh:''}[this.com.localId]);
        }
      },
      this.isServer
    );
  }
}
