import { Component, Input, OnInit, Output } from '@angular/core';
import { ComService } from 'src/app/core/com.service';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css'],
})
export class HeaderComponent implements OnInit {
  @Input() show: boolean;
  @Input() isServer: boolean;
  @Input() myID: any;

  stateMenuShow: boolean = false;

  constructor(public com: ComService) {}

  ngOnInit(): void {}
  showStateMenu() {
    if (!this.com.reconnect) {
      this.stateMenuShow = !this.stateMenuShow;
    }
  }
  logout() {
    // 
    this.com.logout();
    this.com.isLogin = false;
  }
}
